<template>
  <div class="namecanvas-wrapper">
    <div class="namecanvas-content">
      <p>
        <span class="out">当时间走到</span>
        <span id="i">5</span>
        <span class="out">:</span>
        <span id="love">2</span>
        <span id="you">0</span>
        <span class="out">分时，我会告诉你一个秘密哦！</span>
      </p>
    </div>
    <canvas id="scene"></canvas>
    <audio
      id="bgm"
      src="https://oss.xeblog.cn/prod/69d2bf107ba24b8c1df9f50dff02406d.mp3"
      loop
    ></audio>
  </div>
</template>

<script>
import $ from "jquery";

let name = "我爱·恋恋";
let flag = false;
let count = 0;
function getParam(key) {
  let reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)");
  let r = window.location.search.substr(1).match(reg);
  if (r != null) {
    return decodeURI(r[2]);
  }

  return null;
}

export default {
  data() {
    return {};
  },
  props:['isTrigger'],
  components: {},
  methods: {
    show() {
      const self = this;
      if (flag) {
        return;
      }

      let nowDate = new Date();
      let h = nowDate.getHours();
      let m = nowDate.getMinutes();
        
      if (this.isTrigger) {
      // if (
      //   (h == 5 && m == 20) ||
      //   (h == 14 && m == 0) ||
      //   (h == 14 && m >= 0) ||
      //   h > 14
      // ) {
      // if (
      //   (h == 5 && m == 20) ||
      //   (h == 17 && m == 20) ||
      //   (h == 17 && m >= 20) ||
      //   h > 17
      // ) {
        flag = true;
        document.getElementById("bgm") && document.getElementById("bgm").play();
        $(".out").fadeOut(5000, function() {
          $("#scene").fadeIn(2000, function() {
            $("#i").text("I ");
            $("#love").text("Love ");
            $("#you").text("You");
            count++;
            self.showName();
          });
        });
      }

      setTimeout(this.show, 1500);
    },
    showName() {
      if (count > 1) {
        return;
      }

      let canvas = document.querySelector("#scene"),
        ctx = canvas.getContext("2d"),
        particles = [],
        amount = 0,
        mouse = {
          x: 0,
          y: 0,
        },
        radius = 0.7; //Init radius of the force field

      let colors = [
        "rgba(26, 188, 156, .6)",
        "rgba(46, 204, 113, .4)",
        "rgba(52, 152, 219, .4)",
        "rgba(52, 152, 219, .4)",
        "rgba(241, 196, 15, .6)",
        "rgba(231, 76, 60, .4)",
      ];

      let colorsTwo = [
        "rgba(26, 188, 156, 1)",
        "rgba(46, 204, 113, 1)",
        "rgba(52, 152, 219, 1)",
        "rgba(52, 152, 219, 1)",
        "rgba(241, 196, 15, 1)",
        "rgba(231, 76, 60, 1)",
      ];

      let initSize = Math.floor(Math.random() * 0.6) + 1;
      let hoverSize = initSize + 0.7;

      let ww = (canvas.width = window.innerWidth);
      let wh = (canvas.height = window.innerHeight);

      function Particle(x, y) {
        this.x = Math.random() * ww;
        this.y = Math.random() * wh;
        this.dest = {
          x: x,
          y: y,
        };
        //this.r = Math.random() * 1; // the size of bubbles
        this.vx = (Math.random() - 0.5) * 2;
        this.vy = (Math.random() - 0.5) * 2;
        this.accX = 0;
        this.accY = 0;
        this.friction = Math.random() * 0.015 + 0.94; // force of bounce, just try to change 0.015 to 0.5

        //this.color = colors[Math.floor(Math.random() * 10)];
        //this.colorTwo = colorsTwo[Math.floor(Math.random() * 10)];
      }

      Particle.prototype.render = function() {
        this.accX = (this.dest.x - this.x) / 200; //acceleration for X
        this.accY = (this.dest.y - this.y) / 200; //acceleration for Y
        this.vx += this.accX;
        this.vy += this.accY;
        this.vx *= this.friction;
        this.vy *= this.friction;

        this.x += this.vx;
        this.y += this.vy;

        ctx.fillStyle = this.color;
        ctx.beginPath();
        ctx.arc(this.x, this.y, this.r, Math.PI * 2, false);
        ctx.fill();

        let a = this.x - mouse.x;
        let b = this.y - mouse.y;

        let distance = Math.sqrt(a * a + b * b);
        if (distance < radius * 70) {
          this.accX = (this.x - mouse.x) / 20; //acceleration on mouseover X, smaller faster
          this.accY = (this.y - mouse.y) / 20; //acceleration on mouseover Y, smaller faster
          this.vx += this.accX;
          this.vy += this.accY;
          //ctx.fillStyle = this.colorTwo;
        }

        if (distance < radius * 70) {
          this.colorTwo = colorsTwo[Math.floor(Math.random() * 10)];
          ctx.fillStyle = this.colorTwo;
          this.r = hoverSize; // the size of bubbles
        }

        if (distance > radius * 70) {
          this.colorOne = colors[Math.floor(Math.random() * 10)];
          ctx.fillStyle = this.colorOne;
          this.r = initSize;
        }
      };

      function onMouseMove(e) {
        document.getElementById("bgm").play();
        mouse.x = e.clientX;
        mouse.y = e.clientY;
      }

      function initScene() {
        ww = canvas.width = window.innerWidth;
        wh = canvas.height = window.innerHeight / 2;

        ctx.clearRect(0, 0, canvas.width, canvas.height);

        ctx.font = "bold " + ww / 10 + "px sans-serif"; // Size of the text
        ctx.textAlign = "center";
        ctx.fillText(name, ww / 2, wh / 2); //Centering

        let data = ctx.getImageData(0, 0, ww, wh).data;
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.globalCompositeOperation = "screen";

        particles = [];
        for (let i = 0; i < ww; i += Math.round(ww / 400)) {
          //400 here represents the amount of particles
          for (let j = 0; j < wh; j += Math.round(ww / 400)) {
            if (data[(i + j * ww) * 4 + 3] > 250) {
              particles.push(new Particle(i, j));
            }
          }
        }
        amount = particles.length;
      }

      function onMouseClick() {
        radius = 4; //onclick expand radius
      }

      function offMouseClick() {
        radius = 0.5; //offClick init radius
      }

      function delayedInitRadius() {
        setTimeout(offMouseClick, 500); //delay for offClick init radius
      }

      function render(a) {
        requestAnimationFrame(render);
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        for (let i = 0; i < amount; i++) {
          particles[i].render();
        }
      }

      window.addEventListener("resize", initScene);
      window.addEventListener("mousemove", onMouseMove);
      window.addEventListener("mousedown", onMouseClick);
      window.addEventListener("mouseup", delayedInitRadius);
      initScene();
      requestAnimationFrame(render);
    },
  },
  created() {
    const self = this;
    this.$nextTick(() => {
      $(function() {
        let val = getParam("for");
        name = val == null || val.trim().length < 1 ? name : val;
        self.show();
      });
    });
  },
};
</script>

<style type="text/css" lang="less">

.namecanvas-wrapper{
  display: flex;
  flex-direction: column;
  .namecanvas-content{
    text-align: center;
  }
}

</style>
